@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800;900&family=Playfair+Display:wght@400;500;600;700;800;900&family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

.line {
  width: 50px;
  height: 2px;
  background: #FFFFFF;
  opacity: 0.4;
  margin-left: 20px;
}
#top {
  height: 100vh;
  overflow: hidden;
  position: relative;
}

#top::before {
  content: '';
  position: absolute;
  height: 100%;
  width: 400px;
  background: rgba(41, 43, 55, 0.9);
  z-index: 2;
}

#top::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: linear-gradient(251.95deg, rgba(0, 0, 0, 0.76) 10.86%, rgba(0, 0, 0, 0) 78.5%);
  top: 0;
  left: 0;
}

#top .top-bg-img {
  position: absolute;
  top: 0;
  min-width: 100%;
  min-height: 100%;
}

#top h1,
#top div,
#top span,
#top p {
  position: relative;
  z-index: 11;
}

.top-summerice {
  margin-left: 12%;
  margin-top: 150px;
}

.top-title-one {
  font-weight: bold;
  font-size: 14px;
  color: #FFFFFF;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Montserrat', sans-serif;
  margin-bottom: 40px;
}

.top-title-two {
  font-family: 'Playfair Display', serif;
  font-weight: bold;
  font-size: 44px;
  color: #F6AE2D;
  margin-bottom: 20px;
}

.top-dicription {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  width: 46%;
}

.nav-wrapper {
  position: absolute !important;
  width: 410px;
  height: 410px;
  display: -ms-grid;
  display: grid;
  place-items: center;
  border-radius: 100%;
  border: 3px solid #9A9A9A;
  top: -100px;
  right: -100px;
}

.nav-wrapper::before {
  content: '';
  border: 3px solid #292B37;
  border-radius: 100%;
  width: 95%;
  height: 95%;
  position: absolute;
  top: 8px;
}

.the_logo {
  display: block;
  position: relative;
  top: 10px;
  left: -25px;
  z-index: 99;
}

.the_logo img {
  min-width: 100px !important;
  width: 120px;
}

.nav_links {
  position: absolute !important;
  width: 100%;
  height: 100%;
}

.nav_links a {
  font-weight: bold;
  font-size: 14px;
  color: #F6AE2D;
  font-family: 'Roboto', sans-serif;
}

.nav_links a[name="refresh"] {
  position: absolute;
  top: 125px;
  -webkit-transform: rotate(114deg);
          transform: rotate(114deg);
  left: 12px;
}

.nav_links a[name="services"] {
  position: absolute;
  top: 250px;
  -webkit-transform: rotate(70deg);
          transform: rotate(70deg);
  left: 6px;
}

.nav_links a[name="portfolio"] {
  position: absolute;
  top: 350px;
  -webkit-transform: rotate(28deg);
          transform: rotate(28deg);
  left: 85px;
}

.nav_links a[name="about_me"] {
  position: absolute;
  top: 363px;
  -webkit-transform: rotate(-16deg);
          transform: rotate(-16deg);
  left: 215px;
}
